import back from '@/components/back'
import noData from '@/components/noData'

export default {
	components: {
		back,
		noData
	},
	data() {
		return {
			title: '我的处方',
			user: this.$store.state.userInfo.Data,
			haveMore: true,
			page: 1,
			list: [],
			reason: [],
			pageShow: false,
			sorList: ['处方类型', '审方状态', '订单状态'],
			dateList: [{
					name: '全部',
					val: '-1'
				},
				{
					name: '西药处方',
					val: '4'
				},
				{
					name: '中药处方',
					val: '17'
				}
			],
			statusList: [{
					name: '全部',
					val: '-1'
				},
				{
					name: '待审核',
					val: '0'
				},
				{
					name: '审核通过',
					val: '1'
				},
				{
					name: '审核不通过',
					val: '6'
				}
			],
			buyList: [{
					name: '全部',
					val: '-1'
				},
				{
					name: '未支付',
					val: '0'
				},
				{
					name: '待发货',
					val: '1'
				},
				{
					name: '待收货',
					val: '2'
				},
				{
					name: '已完成',
					val: '3'
				},
				{
					name: '已退款',
					val: '5'
				},
				{
					name: '已取消',
					val: '7'
				},
				{
					name: '已关闭',
					val: '8'
				}
				
			],
			date: {
				name: '全部',
				val: '-1'
			},
			status: {
				name: '全部',
				val: '-1'
			},
			buy: {
				name: '全部',
				val: '-1'
			},
			idx: null,
			search: '',
			scrollTopDistance: 0,
			show3: false,
			loading: false,
		}
	},
	created() {
		this.pull(false)
		// if(this.$route.query.fromTCM) {
		// 	this.title = "我的中药处方"
		// }
	},
	mounted() {
		this.getMore()
		this.reload()
		this.scrollIos()
	},
	beforeRouteLeave(to, from, next) {
		window.onscroll = null
		if(from.meta.keepAlive) {　
			from.meta.scollTopPosition = this.scrollTopDistance;
		}
		next()
	},
	beforeRouteEnter(to, from, next) { //进入前    	
		next(vm => {
			vm.getMore()
			setTimeout(() => {
				console.log(to.meta.scollTopPosition)
				vm.$refs.el3.scrollTop = to.meta.scollTopPosition;
			}, 1)
		});
	},
	beforeDestroy() {
		window.onscroll = null
		this.idx = null
	},
	methods: {
		searchFor() {
			this.page = 1
			this.haveMore = true
			this.pull(false)
		},
		pull(h) {
			let _url = "PlatFormAPI/PayOrder/QueryDoctorPrescriptionMergePage"
			// if(this.$route.query.fromTCM) {
			// 	_url = "Prescription/Prescription/GetTCMPrescriptionListForDoctor"
			// } else {
			// 	// _url = "Prescription/Prescription/GetPrescriptionListForDoctor"
			// 	_url = "PlatFormAPI/PayOrder/QueryDoctorPrescriptionMergePage"
			// }
			if(this.haveMore) {
				this.$post(_url, {
					DrId: this.user.User.DoctorId,
					ServiceType: this.date.val,
					ServiceStatus: this.status.val,
					OrderStatus: this.buy.val,
					PageIndex: this.page,
					PageSize: 15,
					AppVersion: '1.5',
					SearchContent: this.search
				}).then(o => {
					this.pageShow = true
					this.show3 = false
					this.loading = false
					if(!o.ReturnData || o.ReturnData.length < 15) {
						this.haveMore = false
					} else {
						this.haveMore = true
					}
					if(h) {
						o.ReturnData.forEach(v => {
							if (v.ExtendJson) {
								v.ExtendJson = JSON.parse(v.ExtendJson)
							}
							this.list.push(v)
						})
						
					} else {
						if(o.ReturnData) {
							o.ReturnData.forEach(item => {
								if (item.ExtendJson) {
									item.ExtendJson = JSON.parse(item.ExtendJson)
								}
							})
							this.list = o.ReturnData
						}else {
							this.list = []
						}
						this.$nextTick(() => {
							window.scrollTo(0, 5)
						})
					}
				})
			}
		},
		sort(idx) {
			if(this.idx == idx) {
				this.idx = null
			} else {
				this.idx = idx
			}
		},
		chose(i) {
			this.date = i
			this.sorList[this.idx] = i.name
			this.idx = null
			this.haveMore = true
			this.page = 1
			this.pull(false)
		},
		chose1(i) {
			this.status = i
			this.sorList[this.idx] = i.name
			this.idx = null
			this.haveMore = true
			this.page = 1
			this.pull(false)
		},
		chose2(i) {
			this.buy = i
			this.sorList[this.idx] = i.name
			this.idx = null
			this.haveMore = true
			this.page = 1
			this.pull(false)
		},
		getDetail(i) {  
			if (i.ServiceType == '17'){ // 中药处方
				this.$router.push(`/chineseMed/prescription?id=${i.ExtendJson.PrescriptionId}`)
			} else if (i.ServiceType == '4') { // 西药处方
				this.$router.push(`/PrescriptionLlist?id=${i.ExtendJson.PrescriptionId}`)
			}
			// if(this.$route.query.fromTCM) {
			// 	this.$router.push(`/chineseMed/prescription?id=${i.Id}`)
			// } else {
			// 	this.$router.push(`/PrescriptionLlist?id=${i.Id}`)
			// }
		},
		getMore() {
			const el3 = this.$refs.el3
			let timer = null
			el3.onscroll = () => {
				clearTimeout(timer)
				timer = setTimeout(() => {
					const top = el3.scrollTop ||
						el3.pageYOffset ||
						el3.scrollTop,
						h = el3.scrollHeight,
						wh = window.innerHeight
					this.scrollTopDistance = top;
					if(h - top - wh < 100 && this.haveMore) {
						this.page += 1
						this.loading = true
						this.pull(true)
					}
				}, 100)
			}
		},
		reload() {
			const el2 = this.$refs.el2
			const el3 = this.$refs.el3
			let startX = null
			let startY = null
			let endX = null
			let endY = null
			el2.addEventListener('touchstart', e => {
				this.show3 = false
				startX = e.changedTouches[0].pageX
				startY = e.changedTouches[0].pageY
			})
			el2.addEventListener('touchmove', e => {
				endX = e.changedTouches[0].pageX
				endY = e.changedTouches[0].pageY
				if(this.idx !== null) {
					return
				}
				if(Math.abs(startX - endX) < Math.abs(startY - endY) && (startY - endY) < 0) {
					const top = el3.scrollTop ||
						el3.pageYOffset
					if(top < 10 || !top) {
						this.show3 = true
					}
				}
			})
			el2.addEventListener('touchend', () => {
				if(this.show3) {
					this.page = 1
					this.haveMore = true
					this.pull(false)
				}
			})
		},
		scrollIos(){
			//ios页面卡死问题
			let el3 = this.$refs.el3
			let lastY = 0;
			el3.addEventListener('touchstart', function(event) {
				lastY = event.touches[0].clientY;
			});
			//获取滚动元素
			let scrollEle =this.$refs.el3
			//先移除监听事件
			scrollEle.removeEventListener('touchmove', function (event) {
				event.preventDefault();
			}, false);
			//再添加监听事件
			scrollEle.addEventListener('touchmove', function(event) {
				let top = event.touches[0].clientY;
				let scrollTop = scrollEle.scrollTop;
				let scrollHeight = scrollEle.scrollHeight;
				let clientHeight = scrollEle.clientHeight;
				let direction = (lastY - top) < 0 ? "up" : "down";
				if (scrollTop == 0 && direction == "up") {
				  event.preventDefault();
				} else if (scrollTop >= (scrollHeight - clientHeight) && direction == "down") {
				  event.preventDefault();
				}
				lastY = top;
			});
		  
		}
	}
}



